    },

    displayedStickers() {
      const start = this.stickerPage * this.stickersPerPage
      return this.stickers.slice(start, start + this.stickersPerPage)
    },
    // 
    visiblePosts() {
      return this.posts.slice(0, this.loadedCount);
    },

    displayName() {
      return this.localDisplayName;
    },
    allPhotos() {
      const out = [];
      this.posts.forEach(post => {
        post.imgs.forEach((url, idx) => {
          out.push({
            url,
            date: new Date(post.ts).toISOString().slice(0, 10),
            post,
            idx
          });
        });
      });
      return out;
    },


    groupedPhotos() {
      const g = {};
      this.allPhotos.forEach(p => {
        const key = p.date.slice(0, 7);
        if (!g[key]) g[key] = [];
        g[key].push({ ...p, meta: p.date, });
      });
      return g;
    },
    allowedBadges() {
      return this.BADGES.filter(b => (b.id === 'best' ? this.currentUser === BEST_BADGE_UID : true));
    },
    hasUnread() {
      return this.posts.some(p => !this.readIds.has(p.id) && p.uid !== this.currentUser);
    },
    bgStyle() {
      return {
        backgroundImage: this.bgSrc ? `url(${this.bgSrc})` : 'none',
        opacity: this.bgOpacity,
        filter: `blur(${this.bgBlur}px)`
      };
    },
    petSVG() {
      return this.petType === 'bird'
        ? `<svg viewBox="0 0 120 120"><circle cx="60" cy="60" r="55" fill="#cdeffd" stroke="#333" stroke-width="3"/><path d="M40 70 Q60 90 80 70" stroke="#333" stroke-width="5" fill="none" stroke-linecap="round"/><circle cx="45" cy="55" r="8"/><circle cx="75" cy="55" r="8"/></svg>`
        : `<svg viewBox="0 0 120 120"><circle cx="60" cy="60" r="55" fill="#ffe4e1" stroke="#333" stroke-width="3"/><circle cx="45" cy="50" r="10"/><circle cx="75" cy="50" r="10"/><path d="M45 80 Q60 95 75 80" stroke="#333" stroke-width="4" fill="none" stroke-linecap="round"/></svg>`;
    },
  },

  watch: {
    theme(val) {
      //  theme  'dark' / 'light' DOM  localStorage
      document.body.classList.toggle('dark', val === 'dark');
      localStorage.setItem('theme', val);
    },
    bgOpacity: 'saveBgOpacity',
    bgBlur: 'saveBgBlur',
    loadMode(val) {
      localStorage.setItem('loadMode', val);
      this.updateLoadBehavior();
    },
    imageInsertMode(val) {
      localStorage.setItem('imageInsertMode', val);
    },

    //  localStorage
    localDisplayName(newName) {
      localStorage.setItem('displayName_' + (this.currentUser || ''), newName);
    },

    selectedBadge(newVal) {
      // 
      this.userBadges[this.currentUser] = newVal;
      localStorage.setItem('userBadges', JSON.stringify(this.userBadges));
    }
  },



  /* ---------- methods ---------- */
  methods: {
    //   JSON.parse 
    safeParse(key, defaultVal) {
      try {
        const raw = localStorage.getItem(key);
        if (raw === null) return defaultVal;
        try {
          //  JSON parse
          return JSON.parse(raw);
        } catch {
          //  JSON
          return raw;
        }
      } catch (e) {
        console.error(`safeParse failed for ${key}:`, e);
        return defaultVal;
      }
    },

    /* ==========  ========== */
    /*  */
    handleLogin(uid) {
      this.currentUser = uid;
      localStorage.setItem('currentUser', JSON.stringify(uid));
      this.readIds = new Set(JSON.parse(localStorage.getItem('readIds_' + uid) || '[]'));
      this.localDisplayName = localStorage.getItem('displayName_' + uid) || '';
      this.posts = this.safeParse('posts', []);
    },
    logout() {
      localStorage.removeItem('currentUser');
      this.currentUser = null;
      this.posts = [];
      this.localDisplayName = '';
      this.readIds = new Set();
    },
    /* ==========  ========== */
    formatMeta(post) {
      const d = new Date(post.ts);
      const date = d.toLocaleDateString();      // 
      const time = d.toLocaleTimeString();      // 
      return `${date} ${time}`;
    },

    //    //
    handleClickToCloseSticker(e) {
      if (this.stickerPickerVisible &&
        !e.target.closest('.sticker-picker') &&
        !e.target.closest('.emoji-fab')) {
        this.stickerPickerVisible = false;
      }
    },

    //    //
    handleClickToCloseNavDropdown(e) {
      if (this.navDropdownVisible &&
        !e.target.closest('.nav-avatar')) {
        this.navDropdownVisible = false;
      }
    },

    //    //
    handleGlobalClick(e) {
      this.handleClickToCloseSticker(e);
      this.handleClickToCloseNavDropdown(e);
    },

    badgeHTML(uid) {
      const val = this.userBadges[uid] || 'none';
      // 
      if (val === 'none') return '';
      //  best  catgirl 
      const cls = val === 'best' ? 'badge best'
        : /* catgirl */      'badge catgirl';
      const name = this.BADGES.find(b => b.id === val)?.name || '';
      return `<span class="${cls}">${name}</span>`;
    },

    scrollTo(id) { const el = document.getElementById(id); if (el) el.scrollIntoView({ behavior: 'smooth' }); },

    // Modal  Picker
    toggleModalPicker() {
      this.modalPickerVisible = !this.modalPickerVisible;
    },
    getAvatar(uid) {
      if (!this.avatarMap[uid]) {
        this.avatarMap[uid] = localStorage.getItem('avatar-' + uid) || 'https://placehold.co/60';
      }
      return this.avatarMap[uid];
    },


    getDisplayName(uid) {
      if (uid === this.currentUser) {
        return this.displayName;
      }
      return localStorage.getItem('displayName_' + uid) || uid;
    },

    toggleNavDropdown() {
      this.navDropdownVisible = !this.navDropdownVisible;
    },
    toggleComments(post) {
      // Vue3 
      this.visibleComments[post.id] = !this.visibleComments[post.id];
    },

    /* ==========  ========== */
    handleInput(e) {
      const nodes = e.target.childNodes
      let txt = ''
      nodes.forEach(n => {
        if (n.nodeType === 3) {                    // 
          txt += n.textContent
        } else if (n.tagName === 'IMG' &&
          n.classList.contains('inline-sticker')) {
          txt += `![](${n.src})`
        } else if (n.tagName === 'BR') {
          txt += '\n'
        }
      })
      this.newPostText = txt
    },
    handlePostImages(e) {
      const files = Array.from(e.target.files).slice(0, 50);

      //     <img>
      if (this.imageInsertMode === 'inline') {
        files.forEach(f => {
          const url = URL.createObjectURL(f);
          this.draftImgs.push(url);

          //   <img.inline-sticker>  
          this.restoreCaret();
          const sel = window.getSelection();
          if (!sel || !sel.rangeCount) return;
          const range = sel.getRangeAt(0);
          const img = document.createElement('img');
          img.src = url;
          img.className = 'inline-sticker';
          img.contentEditable = false;
          range.insertNode(img);
          range.collapse(false);

          // 
          this.insertAtCaret(' ');
        });

        // DOM  markdown 
        this.handleInput({ target: this.$refs.postInput });
      }

      //    
      else {
        this.draftImgs = [];
        files.forEach(f => this.draftImgs.push(URL.createObjectURL(f)));
      }
    },

    removeDraft(i) { this.draftImgs.splice(i, 1); },
    autoResize(e) {
      const el = e.target;
      el.style.height = 'auto';
      el.style.height = el.scrollHeight + 'px';
    },
    /*    */
    saveCaret() {
      const sel = window.getSelection()
      if (sel && sel.rangeCount) {
        this.savedRange = sel.getRangeAt(0).cloneRange()
      }
    },
    restoreCaret() {
      const box = this.$refs.postInput
      // 1)  2)   
      if (!this.savedRange || !box.contains(this.savedRange.startContainer)) {
        this.savedRange = document.createRange()
        this.savedRange.selectNodeContents(box)
        this.savedRange.collapse(false)
      }
      const sel = window.getSelection()
      sel.removeAllRanges()
      sel.addRange(this.savedRange)
      box.focus()
    },
    //  post  ts
    startEdit(post) {
      this.editingPost = post;
      this.newPostText = post.txt;
      this.draftImgs = [...post.imgs];

      //  contenteditable  markdown 
      if (this.$refs.postInput) {
        const html = this.renderText(post.txt).replace(/\n/g, '<br>');
        this.$refs.postInput.innerHTML = html;
      }

      // 
      this.scrollTo('moments');
    },

    publishPost() {
      if (!this.currentUser) return alert('');

      this.isPublishing = true;
      this.isListLoading = true;

      const txt = this.newPostText.trim();

      if (!txt && !this.draftImgs.length) {
        this.isPublishing = false;
        this.isListLoading = false;
        return alert('~');
      }

      if (this.editingPost) {
        this.editingPost.txt = txt;
        this.editingPost.imgs = [...this.draftImgs];

        //  imgs
        localStorage.setItem('posts', JSON.stringify(this.posts));

        this.editingPost = null;
      } else {
        // 
        const post = {
          id: crypto.randomUUID(),
          uid: this.currentUser,
          txt,
          imgPlaces: this.draftImgs.map(() => null),
          imgs: [...this.draftImgs],
          ts: Date.now(),
          views: 0,
          cmts: []
        };

        this.posts.unshift(post);

        //  imgs 
        localStorage.setItem('posts', JSON.stringify(this.posts.map(p => ({ ...p, imgs: [] }))));
      }

      // 
      this.newPostText = '';
      this.newPostPlace = '';
      this.draftImgs = [];

      //  contenteditable
      if (this.$refs.postInput) {
        this.$refs.postInput.innerHTML = '';
      }

      setTimeout(() => {
        this.isPublishing = false;
        this.isListLoading = false;
        this.editingPost = null;
        // this.scrollTo('post-list');
      }, 300);
    },

    handlePostEnter(e) {
      if (!e.shiftKey) { e.preventDefault(); this.publishPost(); }
    },
    deletePost(p) {
      if (confirm('')) {
        this.posts = this.posts.filter(x => x.id !== p.id);
        localStorage.setItem('posts', JSON.stringify(this.posts.map(q => ({ ...q, imgs: [] }))));
      }
    },
    /* ===  === */
    toggleStickerPicker() {
      if (!this.stickerPickerVisible) {      // 
        this.saveCaret();                    //  Range
      }
      this.stickerPage = 0;
      this.stickerPickerVisible = !this.stickerPickerVisible;
    },

    //  
    nextStickerPage() {
      if ((this.stickerPage + 1) * this.stickersPerPage < this.stickers.length)
        this.stickerPage++
    },

    //  
    prevStickerPage() {
      if (this.stickerPage > 0) this.stickerPage--
    },

    //   textarea emoji 
    insertAtCaret(txt) {
      const sel = window.getSelection()
      if (!sel || !sel.rangeCount) return
      const range = sel.getRangeAt(0)
      range.deleteContents()
      range.insertNode(document.createTextNode(txt))
      // 
      range.collapse(false)
      sel.removeAllRanges()
      sel.addRange(range)
      // 
      this.newPostText = this.$refs.postInput.innerText
    },


    selectSticker(s) {
      /* ---------- 1.  ---------- */
      this.restoreCaret()                 //   saveCaret() 

      const sel = window.getSelection()
      if (!sel || !sel.rangeCount) return
      const range = sel.getRangeAt(0)

      /* ---------- 2.  <img>  ---------- */
      const img = document.createElement('img')
      img.src = s.url
      img.className = 'inline-sticker'
      img.contentEditable = false        // 
      range.deleteContents()              // 
      range.insertNode(img)

      /* ---------- 3.  <img>  ---------- */
      const space = document.createTextNode(' ')
      range.setStartAfter(img)
      range.insertNode(space)

      const newRange = document.createRange()
      newRange.setStart(space, 1)
      newRange.collapse(true)
      sel.removeAllRanges()
      sel.addRange(newRange)
      this.savedRange = newRange.cloneRange()   // 

      /* ---------- 4.  handleInput  DOM  markdown ---------- */
      this.handleInput({ target: this.$refs.postInput })

      /* ---------- 5.  ---------- */
      this.stickerPickerVisible = false
    },


    renderText(raw) {
      //  <br> markdown  <img>
      return raw
        .replace(/!\[\]\((.+?)\)/g, (_, u) => `<img class="inline-sticker" src="${u}">`)
        .replace(/\n/g, '<br>')
    },

    handleKeydown(event) {
      if (event.key !== 'Backspace') return;
      const sel = window.getSelection();
      if (!sel || !sel.rangeCount) return;

      const range = sel.getRangeAt(0);
      if (!range.collapsed) return;

      let nodeBefore;
      const { startContainer, startOffset } = range;

      // 
      if (startContainer.nodeType === Node.TEXT_NODE && startOffset === 0) {
        nodeBefore = startContainer.previousSibling;
      }
      //  startOffset 
      else if (startContainer.nodeType === Node.ELEMENT_NODE && startOffset > 0) {
        nodeBefore = startContainer.childNodes[startOffset - 1];
      }

      //  IMG
      if (
        nodeBefore &&
        nodeBefore.nodeType === Node.ELEMENT_NODE &&
        nodeBefore.tagName === 'IMG' &&
        nodeBefore.classList.contains('inline-sticker')
      ) {
        event.preventDefault();
        nodeBefore.remove(); //  IMG

        // 
        const newRange = document.createRange();
        newRange.setStart(startContainer, Math.max(0, startOffset - 1));
        newRange.collapse(true);
        sel.removeAllRanges();
        sel.addRange(newRange);

        //  markdown 
        this.handleInput({ target: this.$refs.postInput });
      }
    },

    isRead(id) { return this.readIds.has(id); },

    /* ==========  Modal ========== */
    openModal(post, startIndex = 0) {               //   post 

      this.modalPost = post;
      this.modalImgs = post.imgs;
      this.modalIndex = startIndex;
      this.modalZoom = 1;
      this.showModal = true;
      this.showInfoSidebar = false;
      this.showImageOptions = false;

      this.updateModalMeta();                        // 
    },
    //  
    updateModalMeta() {
      if (!this.modalPost) {
        return;
      }
      const p = this.modalPost;
      const place = p.imgPlaces?.[this.modalIndex] ?? p.place ?? '';
      const d = new Date(p.ts);
      const date = d.toLocaleDateString();
      const time = d.toLocaleTimeString();
      this.modalMeta = place ? `${date} ${time}  ${place}` : `${date} ${time}`;
    },

    prevModalImg() {
      if (this.modalIndex > 0) {
        this.modalIndex--;
        this.updateModalMeta();
      }
    },
    nextModalImg() {
      if (this.modalIndex < this.modalImgs.length - 1) {
        this.modalIndex++;
        this.updateModalMeta();
      }
    },
    closeModal() { this.showModal = false; },
    // Modal  post 

    toggleInfoSidebar() {
      this.showInfoSidebar = !this.showInfoSidebar;
    },

    //  <img> load 
    handleImgLoad(e) {
      const { naturalWidth: w, naturalHeight: h } = e.target;
      this.infoSize = `${w}  ${h}`;
    },

    closeInfoSidebar() {
      this.showInfoSidebar = false;
    },
    /* ========= =  ========== */
    onWheelZoom(e) {
      const delta = e.deltaY > 0 ? -0.1 : 0.1;         // 
      this.modalZoom = this.clampZoom(this.modalZoom + delta);
    },

    clampZoom(v) {                                      // 
      return Math.min(this.maxZoom, Math.max(this.minZoom, parseFloat(v.toFixed(2))));
    },


    // Modal 
    deleteImage() {
      if (!confirm('')) {
        return; // 
      }
      this.modalPost.imgs.splice(this.modalIndex, 1);
      this.modalImgs.splice(this.modalIndex, 1);
      //  storage
      localStorage.setItem('posts', JSON.stringify(this.posts));
      if (this.modalIndex >= this.modalImgs.length) this.modalIndex = this.modalImgs.length - 1;
      this.showImageOptions = false;
      if (this.modalImgs.length === 0) {
        this.showModal = false;
      }

    },

    // 
    updateLoadBehavior() {
      window.removeEventListener('scroll', this.throttledScroll);
      if (this.loadMode === 'auto') {
        window.addEventListener('scroll', this.throttledScroll);
      }
    },

    onScrollLoad() {
      const el = document.getElementById('moments-list');
      if (!el) return;                           //  bail out if its not there yet
      const scrollBottom = window.innerHeight + window.scrollY;
      const listBottom = el.offsetHeight;
      if (scrollBottom >= listBottom - 200) {
        this.loadMore();
      }
    },

    loadMore() {
      this.loadedCount += this.loadStep;
    },


    /* ==========  ========== */
    sendComment(p) {
      const t = this.newComment[p.id];
      if (t && t.trim()) { p.cmts.push({ who: this.currentUser, txt: t.trim() }); this.newComment[p.id] = ''; }
    },
    deleteComment(p, i) { if (confirm('')) p.cmts.splice(i, 1); },
    editComment(p, i) {
      const old = p.cmts[i].txt, neo = prompt('', old);
      if (neo !== null && neo.trim() && neo !== old) p.cmts[i].txt = neo.trim();
    },
    handleCommentEnter(e, post) {
      if (e.shiftKey) return;
      this.sendComment(post);
    },

    /* ==========  ========== */
    changeBackground(e) {
      const f = e.target.files[0]; if (!f) return;
      const r = new FileReader();
      r.onload = ev => { this.bgSrc = ev.target.result; localStorage.setItem('bgSrc', this.bgSrc); };
      r.readAsDataURL(f);
    },

    clearBackground() {
      this.bgSrc = '';
      localStorage.removeItem('bgSrc');
    },
    saveBgOpacity() { localStorage.setItem('bgOpacity', this.bgOpacity); },
    saveBgBlur() { localStorage.setItem('bgBlur', this.bgBlur); },

    /* ==========  /  ========== */
    changeAvatar(e) {
      const f = e.target.files[0]
      if (!f) return
      const r = new FileReader()
      r.onload = ev => {
        const url = ev.target.result
        //   avatarMap  
        this.avatarMap[this.currentUser] = url
        //   localStorage  
        localStorage.setItem('avatar-' + this.currentUser, url)
      }
      r.readAsDataURL(f)
    },


    updateDisplayName() { localStorage.setItem('displayName_' + this.currentUser, this.localDisplayName); },
    openBadgeModal() { this.showBadgeModal = true; },
    closeBadgeModal() { this.showBadgeModal = false; },
    confirmBadge() {
      // 
      this.userBadges[this.currentUser] = this.selectedBadge;
      localStorage.setItem('userBadges', JSON.stringify(this.userBadges));
      alert('');
      this.closeBadgeModal();
    },


    /* ==========  ========== */
    openPasswordModal() { this.showPasswordModal = true; this.oldPassword = this.newPassword = this.confirmPassword = ''; },
    closePasswordModal() { this.showPasswordModal = false; },
    async changePassword() {
      //  PBKDF2  cred_<uid> 
      const credKey = `cred_${this.currentUser}`;
      const credRaw = localStorage.getItem(credKey);
      if (!credRaw) {
        return alert('');
      }
      const { hash: storedHash, salt, iter } = JSON.parse(credRaw);

      // 
      const oldHash = await pbkdf2Hash(this.oldPassword, salt, iter);
      if (oldHash !== storedHash) {
        return alert('');
      }
      // 
      if (this.newPassword !== this.confirmPassword) {
        return alert('');
      }
      if (this.newPassword.length < 4) {
        return alert(' 4 ');
      }
      // 
      const newHash = await pbkdf2Hash(this.newPassword, salt, iter);
      localStorage.setItem(credKey, JSON.stringify({ hash: newHash, salt, iter }));

      alert('');
      this.closePasswordModal();
    },


    /* ========== Admin  ========== */
    openAdminPwdModal(uid) {
      this.adminTargetUid = uid;
      this.adminNewPassword = '';
      this.adminPwdModalVisible = true;
    },
    closeAdminPwdModal() {
      this.adminPwdModalVisible = false;
    },
    addAllowedUid(uid) {
      const u = (uid || '').trim();
      if (!u) return alert(' UID');
      const list = Array.from(new Set([...this.allowedUids, u])); //  
      setAllowedUids(list);              //  localStorage
      this.allowedUids = list;           // 
      this.newAdminUid = '';             // 
      alert(` UID${u}`);
    },

    removeAllowedUid(uid) {
      if (!confirm(` ${uid}`)) return;
      const list = this.allowedUids.filter(u => u !== uid); //  
      setAllowedUids(list);              //  localStorage
      this.allowedUids = list;           // 
      alert(` ${uid}`);
    },

    resetPassword(uid) {
      if (!confirm(` ${uid} `)) return;
      localStorage.removeItem(`cred_${uid}`);
      localStorage.removeItem(`salt_${uid}`);
      alert(` ${uid} `);
    },


    async confirmAdminSetPassword() {
      if (this.adminNewPassword.length < 4) {
        return alert(' 4 ');
      }
      const iter = 100000
      const salt = getOrCreateSalt(this.adminTargetUid)
      const hash = await pbkdf2Hash(this.adminNewPassword, salt, iter)
      localStorage.setItem(`cred_${this.adminTargetUid}`, JSON.stringify({ hash, salt, iter }))


      alert('');
      this.closeAdminPwdModal();
    },

    /* ==========  ========== */
    toggleTheme() {
      // 
      this.theme = 'dark';
      document.body.classList.add('dark');
      localStorage.setItem('theme', 'dark');
    }

  },

  mounted() {
    this.updateLoadBehavior();
    const ctx = require.context(
      '@/assets/stickers/', // 
      true,                        // 
      /\.png$/                     //  .png 
    );
    this.stickers = ctx.keys().map((key, idx) => ({
      id: 'sticker-' + idx,
      url: ctx(key)                // ctx(key)  URL
    }));


    //  
    document.addEventListener('click', this.handleGlobalClick);

    //  context   
    this.throttledScroll = throttle(this.onScrollLoad.bind(this), 200);
    this.updateLoadBehavior();


    // 
    document.body.classList.toggle('dark', this.theme === 'dark');


  },
  beforeUnmount() {
    document.removeEventListener('click', this.handleGlobalClick);
    window.removeEventListener('scroll', this.throttledScroll);
  },

};
</script>
<style>
:root {
  --bg-light: #f5f5f5;
  --bg-dark: #0f0f11;
  --card-light: rgba(255, 255, 255, .75);
  --card-dark: rgba(30, 30, 31, .55);
  --text-light: #333;
  --text-dark: #d2d2d2;
  --primary: #4a90e2;
  --accent: #e91e63;
  --radius: 14px;
  --blur: 16px;
  --glass-border: 1px solid rgba(0, 0, 0, 0.1);
  --login-bg: #f5f5f5;
  --login-text: #333;
  --login-border: rgba(0, 0, 0, 0.1);
  --bg-opacity: .35;
  --bg-blur: 4px;
  --card-hover-light: rgba(255, 255, 255, 0.9);
  --bg-mask-color: rgba(255, 255, 255, 0.4);
}

body.dark {
  background: var(--bg-dark);
  color: var(--text-dark);
  --login-bg: #1c1c1c;
  --login-text: #d2d2d2;
  --login-border: rgba(255, 255, 255, 0.1);
  --card-hover-dark: rgba(60, 60, 61, 0.55);
  --bg-mask-color: rgba(0, 0, 0, 0.4);
}

html,
body {
  margin: 0;
  padding: 0;
  height: 100%;
  font-family: Inter, \"PingFang SC\", sans-serif;
  transition: .3s background-color, .3s color
}

a {
  color: inherit;
  text-decoration: none;
  cursor: pointer
}

.hidden {
  display: none
}

/*  */
#bgLayer {
  position: fixed;
  inset: 0;
  z-index: -1;
  pointer-events: none;
  background: center/cover no-repeat;
  opacity: var(--bg-opacity);
  filter: blur(var(--bg-blur));
  transition: .3s opacity, .3s filter
}

/*  */
nav {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 100;
  display: flex;
  align-items: center;
  justify-content: space-between;
  background: rgba(255, 255, 255, 0.18);
  backdrop-filter: blur(20px);
  border-bottom: var(--glass-border);
  padding: 14px 24px
}

body.dark nav {
  background: rgba(0, 0, 0, 0.22);
  border-bottom: 1px solid rgba(255, 255, 255, 0.1)
}

.logo {
  font-weight: 700;
  font-size: 20px
}

.menu {
  display: flex;
  gap: 18px;
  align-items: center
}

.menu a {
  padding: 6px 12px;
  border-radius: var(--radius);
  transition: .25s background
}

.menu a:hover {
  background: rgba(0, 0, 0, 0.08)
}

body.dark .menu a:hover {
  background: rgba(255, 255, 255, 0.12)
}

.red {
  width: 8px;
  height: 8px;
  border-radius: 50%;
  background: var(--accent);
  margin-left: 4px
}

/* ----------  ---------- */
/*  select  .btn-publish  */
.np-toolbar select,
.setting-item select {
  appearance: none;
  /*  */
  -webkit-appearance: none;
  -moz-appearance: none;

  background: #333;
  /*  .btn-publish  */
  color: #fff;
  /*  */
  border: none;
  /*  */
  border-radius: var(--radius);
  padding: 6px 12px;
  /*  */
  font-size: 14px;
  cursor: pointer;
  background-image: none !important;
}

/*  */
body.dark .np-toolbar select,
body.dark .setting-item select {
  background: #333;
  color: #fff;
}

/*  */
.menu .nav-item.nav-item-submit {
  flex-direction: row;
  /*  icon +  */
  align-items: center;
  /*  */
  gap: 4px;
  /*  */
  padding: 6px 12px;
  /*  */
  min-width: 64px;
  /*  */
}

/*  */
.np-toolbar select:focus,
.setting-item select:focus {
  outline: none;
  border-color: var(--primary);
  box-shadow: 0 0 0 2px var(--primary-alpha, rgba(74, 144, 226, 0.2));
}

/*  Option   */
/*  */
.np-toolbar select option,
.setting-item select option {
  background: inherit;
  /*  select  */
  color: inherit;
  padding: 0.5em 1em;
  line-height: 1.6;
}

/* Hover */
.np-toolbar select option:hover,
.setting-item select option:hover {
  background: rgba(74, 144, 226, 0.1);
  /*  10%  */
}

/*  +  */
body.dark .np-toolbar select option,
body.dark .setting-item select option {
  background: #2a2a2a;
  /*  */
  color: #e0e0e0;
  /*  */
}

/* hover  */
body.dark .np-toolbar select option:hover,
body.dark .setting-item select option:hover {
  background: #3a3a3a;
  /* Hover  10% */
}



/*  1px  */
.np-toolbar select:focus,
.setting-item select:focus {
  outline: none;
  border: 1px solid #000;
}

/*  */
.btn-ghost,
.btn-publish {
  display: inline-flex;
  align-items: center;
  gap: 6px;
  padding: 6px 14px;
  font-size: 14px;
  border-radius: var(--radius);
  cursor: pointer;
  transition: .25s background, .2s transform
}

.btn-ghost {
  background: var(--card-light);
  border: var(--glass-border);
  backdrop-filter: blur(calc(var(--blur)/2))
}

body.dark .btn-ghost {
  background: rgba(255, 255, 255, 0.08);
  border-color: rgba(255, 255, 255, 0.15)
}

.btn-ghost:hover {
  background: rgba(0, 0, 0, 0.08)
}

body.dark .btn-ghost:hover {
  background: rgba(255, 255, 255, 0.15)
}

.btn-publish {
  background: #333;
  color: #fff;
  border: none
}

.btn-publish:hover {
  transform: scale(1.03);
  background: #2a2a2a
}

body.dark .btn-publish {
  background: #444
}

body.dark .btn-publish:hover {
  background: #3a3a3a
}

/*  */
.card {
  background: var(--card-light);
  backdrop-filter: blur(calc(var(--blur)/2));
  border-radius: var(--radius);
  border: var(--glass-border);
  box-shadow: 0 6px 18px rgba(0, 0, 0, 0.1);
  padding: 18px
}

body.dark .card {
  background: var(--card-dark);
  box-shadow: 0 6px 18px rgba(0, 0, 0, 0.45)
}

/*  */
h2.big {
  margin: 70px 0 22px;
  font-size: 26px
}

/*  */
#moments {
  padding: 40px 8%
}

.np-top {
  display: flex;
  flex-direction: column;
  gap: 8px
}

.np-toolbar {
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
  align-items: center
}

.np-top textarea {
  resize: none;
  height: 78px;
  border-radius: var(--radius);
  border: var(--glass-border);
  background: var(--card-light);
  backdrop-filter: blur(calc(var(--blur)/2));
  padding: 10px;
  font-size: 14px;
  width: 100%
}

body.dark .np-top textarea {
  background: var(--card-dark);
  color: var(--text-dark)
}

.char-count {
  font-size: 12px;
  color: #888
}

.np-preview {
  display: flex;
  gap: 8px;
  overflow-x: auto
}

.np-preview img {
  width: 70px;
  height: 50px;
  border-radius: 8px;
  object-fit: cover
}

.np-preview .remove {
  position: absolute;
  top: -6px;
  right: -6px;
  width: 18px;
  height: 18px;
  border-radius: 50%;
  background: rgba(0, 0, 0, 0.6);
  color: #fff;
  font-size: 14px;
  line-height: 18px;
  text-align: center;
  cursor: pointer;
  transition: background .2s
}

.np-preview .remove:hover {
  background: rgba(0, 0, 0, 0.85)
}

.np-preview .thumb {
  position: relative;
}

.np-preview {
  display: flex;
  gap: 8px;
  overflow-x: auto;
  margin-top: 8px;
  /*   */
}


/*  */
#moments-list {
  display: flex;
  flex-direction: column;
  gap: 26px
}

.post .photos {
  display: flex;
  gap: 8px;
  margin-top: 8px;
  overflow-x: auto
}

.post .photos img {
  width: 96px;
  height: 68px;
  border-radius: 8px;
  object-fit: cover
}

.post .body p {
  margin: 0 0 6px;
  white-space: pre-wrap;
  line-height: 1.5
}

.post small {
  display: block;
  margin-top: 4px;
  font-size: 12px;
  color: #888
}

.actions {
  display: flex;
  gap: 8px;
  font-size: 13px;
  margin-top: 8px;
  align-items: center;
  color: var(--primary)
}

.actions svg {
  width: 18px;
  height: 18px;
  fill: currentColor
}

.more {
  cursor: pointer;
  font-size: 18px;
  padding: 2px 6px;
  border-radius: 50%;
  transition: .2s background
}

.more:hover {
  background: rgba(0, 0, 0, 0.08)
}

.post .head {
  position: relative;
}

.post .more {
  position: relative;
  z-index: 101;
  /*  .post-options */
}

.post-options {
  position: absolute;
  top: calc(100% + 4px);
  left: 0;
  background: var(--card-light);
  padding: 4px;
  border-radius: 6px;
  backdrop-filter: blur(calc(var(--blur)/2));
  display: flex;
  flex-direction: column;
  z-index: 100;
  min-width: max-content;
}

.more-wrapper {
  position: relative;
  display: inline-block;
  /*  */
}

body.dark .post-options {
  background: var(--card-dark);
}

.post-options button {
  background: none;
  border: none;
  cursor: pointer;
  text-align: left;
  padding: 4px 8px;
}

#moments-list .post.card {
  width: 100%;
  max-width: 680px;
  margin: 0 auto;
  padding: 24px;
  /* PC */
}





/*  */
.comments {
  margin-top: 8px;
  padding-top: 4px;
  border-top: 1px solid rgba(0, 0, 0, 0.1)
}

.comment {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 4px 0;
  padding: 6px 8px;
  border-radius: 6px;
  background: rgba(0, 0, 0, 0.03);
  font-size: 13px
}

body.dark .comment {
  background: rgba(255, 255, 255, 0.05)
}

.comment-edit,
.comment-delete {
  cursor: pointer;
  padding: 2px 6px;
  border-radius: 4px;
  transition: background .2s
}

.comment-edit:hover,
.comment-delete:hover {
  background: rgba(0, 0, 0, 0.1)
}

.c-input {
  display: flex;
  gap: 8px;
  margin-top: 8px
}

.c-input input {
  flex: 1;
  padding: 6px 10px;
  border-radius: var(--radius);
  border: var(--glass-border);
  background: var(--card-light);
  backdrop-filter: blur(calc(var(--blur)/2));
  font-size: 14px;
  transition: box-shadow .2s
}

body.dark .c-input input {
  background: var(--card-dark);
  color: var(--text-dark)
}

.c-input input:focus {
  outline: none;
  /*  outline */
  box-shadow: none;
  /*  */
  border: 1px solid #000;
  /*  1px  */
}

/*  */
#album {
  padding: 40px 8%
}

.album-tabs {
  display: flex;
  gap: 16px;
  margin-bottom: 18px
}

.album-tabs button {
  background: none;
  border: none;
  font-weight: 600;
  cursor: pointer;
  font-size: 15px;
  padding: 6px 10px;
  border-radius: var(--radius);
  transition: .25s background
}

.album-tabs .on {
  background: rgba(0, 0, 0, 0.08)
}

body.dark .album-tabs button:hover {
  background: rgba(255, 255, 255, 0.1)
}

.grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(180px, 1fr));
  gap: 18px
}

.photo {
  border-radius: var(--radius);
  overflow: hidden;
  position: relative;
  cursor: pointer
}

.photo img {
  width: 100%;
  height: 120px;
  object-fit: cover;
  transition: .3s transform
}

.photo:hover img {
  transform: scale(1.05)
}

.photo span {
  position: absolute;
  bottom: 6px;
  left: 6px;
  background: rgba(0, 0, 0, 0.45);
  color: #fff;
  font-size: 12px;
  padding: 2px 6px;
  border-radius: var(--radius)
}

fieldset {
  border: none;
  padding: 0;
  margin: 0 0 24px
}

legend {
  font-weight: 600;
  font-size: 15px;
  margin-bottom: 8px
}

.setting-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 12px 0
}

.setting-item+.setting-item {
  border-top: 1px solid rgba(0, 0, 0, 0.1);
  padding-top: 12px
}

body.dark .setting-item+.setting-item {
  border-top: 1px solid rgba(255, 255, 255, 0.1)
}

.setting-item input[type=text] {
  width: 60%;
  padding: 6px;
  border-radius: var(--radius);
  border: var(--glass-border);
  background: var(--card-light);
  backdrop-filter: blur(calc(var(--blur)/2))
}

body.dark .setting-item input[type=text] {
  background: var(--card-dark);
  color: var(--text-dark)
}

/*  */
.setting-item input[type="text"]:focus,
.np-top textarea:focus {
  outline: none;
  /*  outline */
  box-shadow: none;
  /*  */
  border: 1px solid #000;
  /* 1px  */
}

/* Badge */
.badge {
  font-size: 10px;
  padding: 2px 4px;
  border-radius: 4px;
  margin-left: 4px;
  display: inline-block;
  min-width: 40px;
  text-align: center;
  color: #fff
}

.badge.best {
  background: linear-gradient(270deg, #4a90e2, #e91e63, #4a90e2);
  background-size: 400% 400%;
  animation: gradient 10s ease infinite
}

.badge.catgirl {
  background: linear-gradient(135deg, #ff87c3, #ffb6c1)
}

.badge.badge-none {
  background: none;
  border: 1px dashed #aaa;
  color: #aaa
}

@keyframes gradient {
  0% {
    background-position: 0% 50%
  }

  50% {
    background-position: 100% 50%
  }

  100% {
    background-position: 0% 50%
  }
}

.admin-row {
  display: flex;
  align-items: center;
  gap: 12px;
}

.admin-uid {
  min-width: 100px;
  font-weight: bold;
}

.admin-buttons {
  display: flex;
  gap: 8px;
  flex-wrap: wrap;
}

/* Modal  */
.modal {
  z-index: 9999;
  position: fixed;
  inset: 0;
  background: rgba(0, 0, 0, 0.55);
  display: flex;
  justify-content: center;
  align-items: center;
  opacity: 0;
  visibility: hidden;
  transition: opacity .25s ease
}

.modal.show {
  opacity: 1;
  visibility: visible
}

/* 1.  Modal  .box  */
.box {
  background: var(--card-light);
  backdrop-filter: blur(var(--blur));
  border: var(--glass-border);
  border-radius: var(--radius);

  /*  */
  width: 600px;
  max-width: 90vw;
  max-height: 80vh;

  overflow: auto;
  padding: 20px;
  position: relative;
}

/* 2.    modal-delete-btn  */
.post-options .trash-btn {
  /*  modal-delete-btn  */
  background: rgba(120, 120, 120, 0.15);
  border: 1px solid rgba(200, 200, 200, 0.4);
  width: 32px;
  height: 32px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  backdrop-filter: blur(4px);
  box-shadow: 0 0 6px rgba(120, 120, 120, 0.6);
  transition: transform .1s, box-shadow .2s, background .2s;
  cursor: pointer;

  /*  /   */
  color: inherit;
}

.post-options .trash-btn:hover {
  background: rgba(120, 120, 120, 0.25);
  transform: scale(1.1);
  box-shadow: 0 0 12px rgba(120, 120, 120, 0.8);
}

/*  svg  */
.post-options .trash-btn svg {
  width: 16px;
  height: 16px;
  stroke: currentColor;
  fill: none;
}

@media (max-width: 480px) {
  .box {
    width: 95vw;
    padding: 12px;
  }
}

body.dark .box {
  background: var(--card-dark);
  border: 1px solid rgba(255, 255, 255, 0.2)
}

.close {
  position: absolute;
  top: 10px;
  right: 16px;
  font-size: 24px;
  cursor: pointer
}

/* Slider Modal  */
.slider-btn {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  background: rgba(0, 0, 0, 0.3);
  border: none;
  color: #fff;
  width: 36px;
  height: 36px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
}

body.dark .slider-btn {
  background: rgba(255, 255, 255, 0.3);
  color: #333;
}

.slider-btn.left {
  left: 10px;
}

.slider-btn.right {
  right: 10px;
}

/*  .box  */
.slider-modal .box {
  position: relative;
}

/*  */
.slider-modal .modal-meta {
  position: absolute;
  bottom: 16px;
  /*  16px */
  left: 50%;
  transform: translateX(-50%);
  margin-top: 0;
  /*  margin-top */
  font-size: 13px;
  padding: 4px 8px;
  /*  */
  border-radius: 4px;
  /*  */
  background: rgba(0, 0, 0, 0.3);
  /*  */
  color: #fff !important;
  /*  */
  z-index: 10;
  /*  */
}

/*  */
body.dark .slider-modal .modal-meta {
  color: #ddd !important;
}

.modal-more {
  position: absolute;
  top: 10px;
  right: 50px;
  cursor: pointer;
}

.info-btn {
  position: absolute;
  top: 10px;
  right: 82px;
  /*   */
  cursor: pointer;
  font-size: 18px;
  padding: 2px 6px;
  border-radius: 50%;
  transition: .2s background;
  color: var(--text-light);
}

body.dark .info-btn {
  color: var(--text-dark);
}

.info-btn:hover {
  background: rgba(0, 0, 0, 0.08);
}

body.dark .info-btn:hover {
  background: rgba(255, 255, 255, 0.12);
}

.info-btn svg {
  width: 20px;
  height: 20px;
  display: block;
  /*  margin  */
  margin-top: 4px;
  /*  icon */
}

.modal-options {
  position: absolute;
  top: 36px;
  right: 50px;
  background: var(--card-light);
  padding: 6px;
  border-radius: 6px;
  backdrop-filter: blur(calc(var(--blur)/2));
  display: flex;
  flex-direction: column;
  gap: 4px;
}

body.dark .modal-options {
  background: var(--card-dark);
}

.modal-options button {
  background: none;
  border: none;
  cursor: pointer;
}

.post-options,
.modal-options {
  color: var(--text-light);
}

body.dark .post-options,
body.dark .modal-options {
  color: var(--text-dark);
}

/*  */
.post-options button,
.modal-options button {
  color: inherit;
}

/* -----------------------------------------------------------------------------
     Override global focus/option styles for any np-toolbar select inside a modal
     ----------------------------------------------------------------------------- */
.modal.show .np-toolbar select:focus {
  outline: none !important;
  /* match the glass-border used everywhere else */
  border: var(--glass-border) !important;
  box-shadow: none !important;
}

.modal.show .np-toolbar select option {
  /*  same flat background & text color as  select options */
  background: var(--card-light) !important;
  color: var(--text-light) !important;
}

body.dark .modal.show .np-toolbar select option {
  background: var(--card-dark) !important;
  color: var(--text-dark) !important;
}

.modal-delete-btn {
  position: absolute;
  bottom: 16px;
  right: 16px;
  width: 40px;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: rgba(120, 120, 120, 0.15);
  /*  */
  border: 1px solid rgba(200, 200, 200, 0.4);
  /*  */
  color: #e0e0e0;
  /*  */
  border-radius: 50%;
  backdrop-filter: blur(4px);
  /*  */
  box-shadow: 0 0 6px rgba(120, 120, 120, 0.6);
  /*  */
  transition: transform .1s, box-shadow .2s, background .2s;
  cursor: pointer;
}

.modal-delete-btn:hover {
  background: rgba(120, 120, 120, 0.25);
  /*  */
  transform: scale(1.1);
  /*  */
  box-shadow: 0 0 12px rgba(120, 120, 120, 0.8);
  /*  */
}

.modal-delete-btn svg {
  width: 20px;
  height: 20px;
  stroke: currentColor;
  /*  color */
  fill: none;
}


@keyframes breathe {

  0%,
  100% {
    transform: translateY(0)
  }

  50% {
    transform: translateY(-4px)
  }
}

/*  */
.fade-enter-active,
.fade-leave-active {
  transition: opacity .3s ease
}

.fade-enter,
.fade-leave-to {
  opacity: 0
}

/*  */
.more,
.modal-more {
  color: var(--text-light);
}

body.dark .more,
body.dark .modal-more {
  color: var(--text-dark);
}

/*  loading */
.spinner {
  display: inline-block;
  width: 16px;
  height: 16px;
  border: 2px solid transparent;
  border-top-color: currentColor;
  border-radius: 50%;
  animation: spin .8s linear infinite;
  vertical-align: middle;
  margin-right: 4px;
}

@keyframes spin {
  to {
    transform: rotate(360deg)
  }
}

/*   +   */
/*    */
.skeleton-list {
  display: flex;
  flex-direction: column;
  gap: 26px;
  align-items: center;
  /*  */
}

.skeleton-list .skeleton-card {
  width: 100%;
  max-width: 680px;
  /*  */
  margin: 0 auto;
  /*  */
}

body.dark .skeleton-card {
  background: var(--card-dark);
  /*  */
}

.skeleton-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg,
      transparent,
      rgba(255, 255, 255, 0.6),
      transparent);
  animation: shimmer 1.2s ease-in-out infinite;
}

.skeleton-head,
.skeleton-body {
  background: rgba(255, 255, 255, 0.2);
  /*  */
  border-radius: 4px;
  margin-bottom: 8px;
}

body.dark .skeleton-head,
body.dark .skeleton-body {
  background: rgba(0, 0, 0, 0.2);
  /*  */
}

.skeleton-head {
  width: 40%;
  height: 16px;
}

.skeleton-body {
  width: 100%;
  height: 60px;
}

/*  */
@keyframes shimmer {
  0% {
    transform: translateX(-100%);
  }

  50% {
    transform: translateX(100%);
  }

  100% {
    transform: translateX(200%);
  }
}

/*  */
.post-fade-enter-active {
  transition: all .3s ease;
}

.post-fade-leave-active {
  transition: all .2s ease;
}

.post-fade-enter {
  opacity: 0;
  transform: translateY(-10px);
}

.modal {
  position: fixed;
  inset: 0;
  background: rgba(0, 0, 0, 0.8);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
}

.lightbox {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  padding: 16px;
}

.lightbox img {
  max-width: 90vw;
  max-height: 90vh;
  object-fit: contain;
  border-radius: 8px;
  box-shadow: 0 8px 24px rgba(0, 0, 0, 0.5);
}

.close-btn {
  position: absolute;
  top: 8px;
  right: 8px;
  background: none;
  border: none;
  font-size: 28px;
  color: #fff;
